* {
  box-sizing: border-box;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #c20583;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

button:hover {
  background-color: #000;
}

input {
  border: 1px solid #c20583;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.color-switcher {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.timer-set {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.timer {
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 10px;
  display: flex;
}

.field {
  flex-direction: column;
  display: flex;
}

.value {
  text-align: center;
  font-size: 40px;
}

.arrow {
  text-align: center;
  color: #000;
  outline: none;
  font-size: 40px;
  text-decoration: none;
  position: relative;
}

.arrow:hover {
  color: #c20583;
}

.tusks {
  text-align: center;
  color: #000;
  outline: none;
  font-size: 40px;
  text-decoration: none;
}

.tusks:hover {
  color: #c20583;
}

.list-of-tusk {
  color: #000;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 100px;
  list-style: none;
  display: flex;
}
